<template>
  <div class="image-rich-text"
    spellcheck="false"
    contenteditable="true"
    :style="element.style"
    v-html="innerText"
    @focus="handleFocus"
    @keydown="$event.stopPropagation()"
    @keyup.stop="emitInput($event)"
    @paste="handlePaste($event)"
    @click.stop>
  </div>
</template>

<script>
import { getPlainText, keepCursorEnd } from '../../utils'

export default {
  name: 'ImageRichText',
  data () {
    return {
      innerText: this.value.replace(/\n/g, '<br>')
    }
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    element: {
      type: Object,
      default: () => {
        return {
          style: {}
        }
      }
    },
    activeEleText: {
      type: Object,
      default: () => {}
    }
  },
  watch: {
    value (val) {
      this.innerText = val
    }
  },
  methods: {
    // 
    emitInput (event) {
      this.$emit('input', event.target.innerHTML)
      this.keepCursorLast(event)
    },
    // 
    handlePaste (event) {
      this.$emit('input', getPlainText(event))
      this.keepCursorLast(event)
    },
    // 
    keepCursorLast (event) {
      this.$nextTick(() => {
        keepCursorEnd(event.target)
      })
    },
    // 
    handleFocus () {
      this.activeEleText.active = true
    }
  }
}
</script>

<style lang="less">
.image-rich-text {
  min-height: 18px;
  padding: 6px 8px!important;
  outline: none;
  word-break: break-all;
}
</style>



