<template>
  <el-upload
    ref="uploadImage"
    class="upload-image-wrapper"
    action=""
    accept=".jpg, .png, .bmp, .jpeg, .webp"
    :multiple="false"
    :show-file-list="false"
    :auto-upload="false"
    :on-change="handleChange">
    <el-button size="small" type="primary">{{ btnName }}</el-button>
  </el-upload>
</template>

<script>
export default {
  name: 'UploadImage',
  props: {
    btnName: {
      type: String,
      default: ''
    }
  },
  methods: {
    handleChange (image) {
      const rawImage = image.raw
      if (!rawImage) return false
      if (!this.isImage(rawImage)) {
        this.$message.warning('.jpg, .png, .bmp, .jpeg, .webp!')
        return false
      }
      if (this.isLimit5M(rawImage)) {
        this.readImage(rawImage)
      }
    },
    isLimit5M (image) {
      const isLimit5M = image.size / 1024 / 1024 < 5
      if (isLimit5M) {
        return true
      } else {
        this.$message.warning('5M!')
        return false
      }
    },
    isImage (image) {
      return /\.(jpg|png|bmp|jpeg|webp)$/.test(image.name)
    },
    readImage (image) {
      const reader = new FileReader()
      reader.onload = e => {
        let data
        if (typeof e.target.result === 'object') {
          // Array Bufferblob base64
          data = window.URL.createObjectURL(new Blob([e.target.result]))
        } else {
          data = e.target.result
        }
        this.$emit('on-success', data)
      }
      // base64
      reader.readAsDataURL(image)
      // blob
      // reader.readAsArrayBuffer(image)
      reader.onerror = e => {
        this.$message.error('!')
      }
      this.$refs.uploadImage.clearFiles()
    }
  }
}
</script>



