<template>
  <div class="image-synthesizer">
    <Hints>
      <template slot="hintName"></template>
      <template slot="hintInfo">
        <p>VueDRR</p>
        <p>VueDRRvue-draggable-resizablevue</p>
      </template>
    </Hints>
    <el-row :gutter="20">
      <el-col :span="16">
        <el-card shadow="always">
          <div slot="header" class="title"></div>
          <div class="box-wrapper">
            <div class="drag-container" :style="containerStyle">
              <ElementDrr
                v-for="(item, index) in elements"
                :key="index"
                :element="item"
                :handles="dragHandles(item.type)"
                :style="elementZIndex(item.type)"
                @updateActiveEle="updateActiveEle">
                <!--  -->
                <img v-if="item.type==='image'" :src="item.src" draggable="false">
                <!--  -->
                <ImageRichText v-if="item.type === 'text'" v-model="item.text" :element="item" :activeEleText="activeEleText"/>
              </ElementDrr>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card shadow="always">
          <div slot="header" class="title"></div>
          <div class="content-box">
            <el-form class="form-wrapper" label-width="70px">
              <el-form-item label="">
                <UploadImage btnName="" @on-success="handleSuccess"/>
              </el-form-item>
              <el-form-item label="">
                <el-button @click="addText"></el-button>
              </el-form-item>
              <el-form-item label="">
                <UploadImage @on-success="handleAddImage"/>
              </el-form-item>
              <el-form-item label="">
                <el-button type="danger" @click="deleteActiveEle"></el-button>
              </el-form-item>
            </el-form>
            <TextSetting v-if="activeEle.type === 'text'" :activeEleText="activeEleText"/>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Hints from '../../components/Hints'
import ElementDrr from '../../components/ElementDrr'
import ImageRichText from '../../components/ImageRichText'
import TextSetting from '../../components/TextSetting'
import UploadImage from '../../components/UploadImage'

export default {
  name: 'ImageSynthesizer',
  components: { Hints, ElementDrr, ImageRichText, TextSetting, UploadImage },
  data () {
    return {
      container: { // 
        width: 0,
        height: 0,
        bgImageSrc: './static/img/crop-image.jpg' // 
      },
      elements: [], // 
      activeEle: {}, // 
      eleNum: 0
    }
  },
  computed: {
    // 
    containerStyle () {
      return {
        width: this.container.width + 'px',
        height: this.container.height + 'px',
        backgroundImage: 'url(\'' + this.container.bgImageSrc + '\')'
      }
    },
    // 
    activeEleText () {
      if (this.activeEle.type === 'text') {
        return this.activeEle
      }
    }
  },
  created () {
    this.getDragContainerSize(1920, 1200)
    this.addText()
    this.addImage({
      active: false,
      src: './static/img/add-image.jpeg',
      size: 53248,
      width: 132,
      height: 132
    })
  },
  methods: {
    // 
    dragHandles (type) {
      if (type === 'text') {
        return ['e', 'w']
      }
    },
    // 
    elementZIndex (type) {
      let zIndex = 1
      switch (type) {
        case 'text':
          zIndex = 3
          break
        case 'image':
          zIndex = 2
          break
      }
      return {
        zIndex: zIndex
      }
    },
    // 
    handleSuccess (data) {
      this.container.bgImageSrc = data
      const image = new Image()
      image.src = data
      image.onload = () => {
        this.getDragContainerSize(image.width, image.height)
      }
    },
    // 
    getImageSize (imageTrueW, imageTrueH, showAreaW, showAreaH) {
      let [width, height] = [0, 0]
      // 
      if (imageTrueW > imageTrueH) {
        if (imageTrueW >= showAreaW) { // 
          const imageRatioH = imageTrueH * (showAreaW / imageTrueW)
          // 
          if (imageRatioH >= showAreaW) {
            width = imageTrueW * (showAreaH / imageTrueH)
            height = showAreaH
          } else {
            width = showAreaW
            height = imageRatioH
          }
        } else {
          width = imageTrueW
          height = imageTrueH
        }
      } else { // 
        if (imageTrueH >= showAreaH) { // 
          width = imageTrueW * (showAreaH / imageTrueH)
          height = showAreaH
        } else {
          width = imageTrueW
          height = imageTrueH
        }
      }
      return {
        width,
        height
      }
    },
    // 
    getDragContainerSize (imageW, imageH) {
      const [showAreaW, showAreaH] = [850, 550]
      const sizeObj = this.getImageSize(imageW, imageH, showAreaW, showAreaH)
      // 
      this.container.width = sizeObj.width
      this.container.height = sizeObj.height
    },
    // 
    addText () {
      let tagNum = ++this.eleNum
      const text = {
        active: true,
        text: '',
        type: 'text',
        tag: 'text_' + tagNum,
        x: 160,
        y: 100,
        w: 180,
        h: 36,
        style: {
          textAlign: 'left',
          lineHeight: '24px',
          fontSize: '24px',
          fontFamily: '',
          fontWeight: 400,
          color: '#f70707',
          backgroundColor: '',
          overflow: 'hidden'
        }
      }
      if (this.elements.length > 4) {
        this.$message.warning('5')
      } else {
        this.elements.push(text)
        this.updateActiveEle(text)
      }
    },
    // 
    addImage (imgObj) {
      let tagNum = ++this.eleNum
      const image = {
        active: imgObj.active,
        type: 'image',
        tag: 'image_' + tagNum,
        x: 490,
        y: 100,
        w: parseInt(imgObj.width),
        h: parseInt(imgObj.height),
        src: imgObj.src,
        size: imgObj.size
      }
      if (this.elements.length > 4) {
        this.$message.warning('5')
      } else {
        this.elements.push(image)
        if (imgObj.active) {
          this.updateActiveEle(image)
        }
      }
    },
    // 
    handleAddImage (data) {
      const dataStrLen = data.replace('data:image/jpeg;base64,', '').length
      const dataSize = parseInt(dataStrLen - (dataStrLen / 8) * 2)
      const img = new Image()
      img.src = data
      img.onload = () => {
        let imageSize = this.getImageSize(img.width, img.height, parseInt(this.container.width / 4), parseInt(this.container.height / 4))
        this.addImage({
          active: true,
          src: data,
          size: dataSize,
          width: imageSize.width,
          height: imageSize.height
        })
      }
    },
    // 
    updateActiveEle (element) {
      this.activeEle = element
    },
    // 
    deleteActiveEle () {
      const newElements = this.elements.filter(item => {
        return item.tag !== this.activeEle.tag
      })
      this.$nextTick(() => {
        this.elements = newElements
      })
      this.updateActiveEle({})
    }
  }
}
</script>

<style lang="less">
.image-synthesizer {
  .title {
    text-align: center;
    font-size: 14px;
    font-weight: bold;
  }
  .box-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 550px;
    overflow: hidden;
    .drag-container {
      position: relative;
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      .z-drr-container {
        border: 1px dashed transparent;
        &.z-active {
          border: 1px dashed #2e95ff;
        }
      }
    }
  }
  .content-box {
    height: 550px;
    overflow: hidden;
    .form-wrapper {
      width: 300px;
      margin: 15px auto 0;
    }
    .el-button {
      width: 210px;
    }
  }
  img {
    width: 100%;
    height: 100%;
  }
}
</style>



