<template>
  <div class="text-setting-wrapper">
    <el-form label-width="70px">
      <!---->
      <el-form-item label="">
        <ImageRichText class="text-editable" v-model="activeEleText.text" :activeEleText="activeEleText"/>
      </el-form-item>
      <!---->
      <el-form-item label="">
        <el-select v-model="activeEleText.style.fontFamily" size="small">
          <el-option
            v-for="item in fontFamilyData"
            :key="item.value"
            :label="item.value"
            :value="item.value"
            :style="{'fontFamily': item.value}">
          </el-option>
        </el-select>
      </el-form-item>
      <!---->
      <el-form-item label="">
        <el-select
          v-model="activeEleText.style.fontSize"
          size="small">
          <el-option
            v-for="item in fontSizeData"
            :key="item.value"
            :label="item.value"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <!---->
      <el-form-item label="">
        <!--  -->
        <div class="color-box">
          <i class="vue-sys-icon-wenzise color-icon" :style="{'color': activeEleText.color}"></i>
          <el-color-picker v-model="activeEleText.style.color"/>
        </div>
        <!--  -->
        <div class="color-box">
          <i class="vue-sys-icon-beijingse color-icon icon-bg" :style="{'color': activeEleText.backgroundColor}"></i>
          <el-color-picker v-model="activeEleText.style.backgroundColor"/>
        </div>
        <div class="style-box">
          <!--  -->
          <span :class="['style-item', fontWeight ? 'active' : '']" @click="setFontWeight">
            <i class="vue-sys-icon-jiacu style-icon"></i>
          </span>
          <!--  -->
          <div class="style-item">
            <el-dropdown trigger="click" @command="handleCommand">
              <i class="vue-sys-icon-duiqi style-icon"></i>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="left" :style="setAlignActive('left')"></el-dropdown-item>
                <el-dropdown-item command="center" :style="setAlignActive('center')"></el-dropdown-item>
                <el-dropdown-item command="right" :style="setAlignActive('right')"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="downloadImage"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { base64toBlob } from '../../utils'
import ImageRichText from '../ImageRichText'

const fontFamilyData = [
  {value: ''},
  {value: ''},
  {value: ''},
  {value: ''},
  {value: ''},
  {value: ''},
  {value: ''}
]
const fontSizeData = [
  {value: '12px'},
  {value: '14px'},
  {value: '16px'},
  {value: '18px'},
  {value: '24px'},
  {value: '32px'},
  {value: '48px'}
]

export default {
  name: 'TextSetting',
  data () {
    return {
      fontFamilyData,
      fontSizeData
    }
  },
  props: {
    activeEleText: {
      type: Object,
      default: () => {}
    }
  },
  computed: {
    // 
    fontWeight () {
      if (+this.activeEleText.style.fontWeight === 400) {
        return false
      } else {
        return true
      }
    }
  },
  watch: {
    // 
    activeEleText: {
      handler (val) {
        this.activeEleText.style.lineHeight = val.style.fontSize
      },
      deep: true
    }
  },
  components: { ImageRichText },
  methods: {
    // 
    setFontWeight () {
      if (+this.activeEleText.style.fontWeight === 400) {
        this.activeEleText.style.fontWeight = 600
      } else {
        this.activeEleText.style.fontWeight = 400
      }
    },
    // 
    handleCommand (command) {
      this.activeEleText.style.textAlign = command
    },
    // 
    setAlignActive (type) {
      if (this.activeEleText.style.textAlign === type) {
        return {
          backgroundColor: '#409eff',
          color: '#fff'
        }
      } else {
        return {
          backgroundColor: '',
          color: '#333'
        }
      }
    },
    // 
    textBecomeImg (obj) {
      let canvas = document.createElement('canvas')
      canvas.width = obj.w
      canvas.height = obj.h
      let context = canvas.getContext('2d')
      // canvas
      let initTop = 4
      let initLeft = 8
      // 
      context.fillStyle = obj.style.backgroundColor || 'transparent'
      context.fillRect(0, 0, canvas.width, canvas.height)
      // 
      context.fillStyle = obj.style.color
      context.font = obj.style.fontWeight + ' ' + obj.style.fontSize + ' ' + obj.style.fontFamily
      context.textBaseline = 'top'
      // 
      context.textAlign = obj.style.textAlign

      let textArr = obj.text.split('')
      let tempStr = ''
      let rowArr = []
      let maxTextWidth = canvas.width - 2 * initLeft

      for (let i = 0; i < textArr.length; i++) {
        if (context.measureText(tempStr).width < maxTextWidth && context.measureText(tempStr + (textArr[i])).width <= maxTextWidth) {
          tempStr += textArr[i]
        } else {
          rowArr.push(tempStr)
          tempStr = textArr[i]
        }
      }
      rowArr.push(tempStr)

      let drawLeft
      if (context.textAlign === 'left') {
        drawLeft = initLeft
      } else if (context.textAlign === 'center') {
        drawLeft = maxTextWidth / 2
      } else if (context.textAlign === 'right') {
        drawLeft = maxTextWidth
      }

      for (let i = 0; i < rowArr.length; i++) {
        context.fillText(rowArr[i], drawLeft, (parseInt(obj.style.fontSize) * i + initTop), maxTextWidth)
      }

      return canvas.toDataURL('image/png')
    },
    // 
    downloadImage () {
      const imageBase64 = this.textBecomeImg(this.activeEleText)
      const imageBlob = base64toBlob(imageBase64)
      const link = document.createElement('a')
      const event = document.createEvent('HTMLEvents')
      // initEvent  
      event.initEvent('click', true, true)
      link.download = 'image.png'
      link.href = URL.createObjectURL(imageBlob)
      // 
      link.dispatchEvent(new MouseEvent('click', {bubbles: true, cancelable: true, view: window}))
    }
  }
}
</script>

<style lang="less">
@import "../../assets/less/text-setting";
</style>



